@import './src/less/variables.less';     //      



.container {
  .container();
}

.row {
  .row();
}



//     XS     
.make-column-list-general-rules(xs); //    .col-xs-...    
.make-columns(@grid-columns, xs);    //     .col-xs-... 
.make-offsets(@grid-columns, xs);    //      .col-xs-offset-...



//     SM     @screen-sm ( : 480px)
.make-column-list-general-rules(sm); //    .col-sm-...    

@media (min-width: @screen-sm) {
  .make-columns(@grid-columns, sm);  //     .col-sm-... 
  .make-offsets(@grid-columns, sm);  //      .col-sm-offset-...
}



//     MD     @screen-md ( : 768px)
.make-column-list-general-rules(md); //    .col-md-...    

@media (min-width: @screen-md) {
  .make-columns(@grid-columns, md);  //     .col-md-... 
  .make-offsets(@grid-columns, md);  //      .col-md-offset-...
}



//     LG     @screen-lg ( : 992px)
.make-column-list-general-rules(lg); //    .col-lg-...    

@media (min-width: @screen-lg) {
  .make-columns(@grid-columns, lg);  //     .col-lg-... 
  .make-offsets(@grid-columns, lg);  //      .col-lg-offset-...
}



//     XL     @screen-xl ( : 1200px)
.make-column-list-general-rules(xl); //    .col-xl-...    

@media (min-width: @screen-xl) {
  .make-columns(@grid-columns, xl);  //     .col-xl-... 
  .make-offsets(@grid-columns, xl);  //      .col-xl-offset-...
}



